.menu {
  display: flex;
  justify-content: center;
  // align-items: center
  flex-direction: column;
  list-style: none;
  gap: 15px;
  margin: 20px 0;
  padding: 0;
  width: 100%;
  li {
    width: 100%;
    position: relative;
    &.menu-item-has-children {
      &:before {
        display: block; } }
    &.active {
      &:before {
        transform: rotate(180deg); } }
    .sub-menu {
      list-style: none;
      display: none;
      margin-left: 12px;
      padding: 0;
      overflow: hidden;
      border-left: 1px dashed $borderColor; }
    &.current_page_item {
 }      // background-color: rgba($accent, 0.1)
    &:before {
      content: '\f078';
      position: absolute;
      font-family: 'FontAwesome';
      right: 10px;
      display: none;
      height: 25px;
      width: 15px; }
    a {
      text-decoration: none;
      font-family: $Roboto;
      font-weight: 700;
      font-size: 16px;
      line-height: 22px;
      color: #241D19;
      display: block;
      width: 100%;
      padding: 5px 15px;
      &:active, &:focus {
        outline: none; }
      &:hover {
        background-color: rgba($accent, 0.1); } } } }
